* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --back: #ACC8E5;
  --text: #112A46;
}

html {
  font-size: 62.5%;
}

.container-fluid {
  max-width: 136.6rem;
  margin: 0 auto;
  padding: 0 5rem;
  background-color: var(--back);
  height: 100vh;
}

.note_body {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
}
.note_body .note_box {
  margin: 0 auto;
}
.note_body .note_box .note_content {
  display: flex;
  justify-content: space-between;
}